:root{
    /* Colors */
    --primary-color:#0a3553;
    --primary-hover-color:#162d3d;
    --error: #e76e6e;
    --white:#fff;
    --active-color:#00bae9;

    /* Font */
    --font: "Poppins", sans-serif;
    --font-size:14px;

    /* Misc */
    --border-radius:5px;
    --border: 3px solid var(--primary-color);
    --error-border: 3px solid var(--error);
}